export default {"name":"六安篮茶","img":"\\heicha\\luanlancha\\img_20191122132514yn8u2p.png","desc":["六安茶之主要产区位于安徽省六安地区，其中以六安、金寨、霍山三县所产之品质最佳。陈旧六安，非产自安徽的六安，而是产自安徽的祁门的芦溪和溶口两乡;该地产茶已有数百年历史。旧六安的制造过程更为烦复，首先杀青-揉捻-晒坯-烘干-筛分和撼簸成为绿茶;经过挑选后再烘-夜露-熏蒸-装篓后再烘一次;制成的茶叶陈放两至三年才推出市场销售。而孙义顺六安，则在瓜片成茶加重焙火后，再蒸压放入小竹篓内，存放长久时间后饮用，制法及贮存饮用方法，较近似普洱茶，所以孙义顺六安，应不属于绿茶类;应和普洱茶归纳为黑茶类。"],"menus":[{"name":"六安篮茶茶叶历史","content":[{"text":"明代"},{"text":"明代闻龙《茶笺》：六安品亦精，入药最效。"},{"text":"六安篮茶，始于明代1630年前后，至今已有300多年历史。其属于传统工艺名茶，内销两广及香港，外销东南亚诸国，颇负盛名，因其具有药用价值，在当地被誉为“圣茶”。"},{"text":"清代"},{"text":"六安篮茶从明代发端，至清光绪以前，盛极一时，早已名扬天下，得到海内外品茗者的追捧。最享盛名的要属“孙义顺”六安篮茶。具记载“安徽孙义顺茶厂向在六安采摘雨前上上嫰芽，加工拣选不惜资本…”。"},{"text":"民国"},{"text":"具史料记载，民国21年就有“六安篮茶”茶号47家，民国22年“六安篮茶”产量已达2000担，在三十年代的电影中仍可看到广东的名门世家开竹篓泡六安篮茶的景象，可见六安篮茶之贵重，极受高端人群的喜爱。"},{"text":"历史记载"},{"text":"20世纪初期，岭南和东南亚一带瘟疫流行，一位姓戴的名医开三钱六安篮茶作药引，效果奇好，治好了不少病人。六安篮茶可以治病的消息，从此不胫而走，名声鹤起。因为其稀少珍贵，在岭南和东南亚一带，不仅是一种上乘饮料，而且被视为良药，尊为“圣茶”。"}]},{"name":"六安篮茶茶叶特点","content":[{"text":"六安篮茶茶性温和，干茶色泽黑亮;茶汤明艳亮丽、深琥珀红色;香气陈醇;滋味浓醇甘爽，入口细滑，持久生津;叶底呈铁锈红色，柔软明亮。"},{"src":"\\heicha\\luanlancha\\img_201911221325142kslv0.jpg"}]},{"name":"六安篮茶加工工艺","content":[{"text":"六安篮茶制作分为采摘、初制和精制。"},{"text":"采摘：谷雨前后10天，采自1芽2叶、1芽3叶或对夹叶;"},{"src":"\\heicha\\luanlancha\\img_20191122132514rv0ytq.jpg"},{"text":"初制：摊青、杀青、揉捻、晒坯(发酵)、烘干;"},{"text":"精制：筛分、风选、挑拣、拼配匀堆、足火、露茶、蒸软、装篓成型、复烘而成。"},{"text":"制成后趁热装入内衬有粽叶的圆形竹篓中压实存放。"}]},{"name":"六安篮茶储藏方法","content":[{"text":"1、干燥箱保存"},{"text":"干燥箱具有温度稳定、隔绝空气的功能，用来保存六安蓝茶也是非常适合的，能有效避免茶叶潮湿或氧化。具体方法为：先用小干燥箱分装少量\n        六安瓜片\n        茶叶，以便随时取用，剩下的则用大干燥箱密封保存。"},{"text":"2、锡罐保存"},{"text":"锡罐具有不透光的功能，也是非常适合用来保存六安蓝茶的。使用锡罐、铁罐保存六安瓜片，包装前需先在罐内垫一层绵纸或牛皮纸，且一定要保持罐内清洁、干燥和无异味。"}]},{"name":"六安篮茶冲泡方法","content":[{"text":"一、由于六安篮茶的口感为甘醇，除了可以用盖碗冲泡之外，紫砂茶壶更为合适，因为粗砂质的紫砂茶壶，透气度比一般的茶壶较高，可以带出陈年六安篮茶的那股清甜爽口之感。"},{"src":"\\heicha\\luanlancha\\img_20191122132514p34lmf.jpg"},{"text":"二、用小的白色品茗杯来喝，因茶杯很薄、很细，很适合品饮，而且可饮赏茶汤的油亮与美色。"},{"text":"冲泡"},{"text":"适量的茶叶及篮内粽叶放入容器，注入 90度开水冲洗茶叶。90度泡约20秒后出茶汤，每泡间隙各增加10秒。六安篮茶极其耐泡，六泡过后茶汤转甜，陈韵留齿久不散。"}]},{"name":"六安篮茶功效作用","content":[{"text":"1、减肥"},{"text":"六安蓝茶当中大量的茶碱和咖啡因能够活化蛋白质激酶及三酸甘油酯解脂酶，从而达到减少脂肪细胞堆积的作用，是减肥的好帮手。"},{"text":"2、抑菌"},{"text":"六安蓝茶中儿茶素能够与细菌中蛋白质反生反应，从而起到抑制效果，改善肠胃消化功能。如急性的细菌腹泻，适量饮用六安蓝茶有明显的缓解效果。"},{"text":"3、降血脂"},{"text":"儿茶素除了能够抑菌，还能够降低总胆固醇、游离胆固醇和三酸甘油酯等，适合高血脂患者饮用，对身体有一定好处。"},{"text":"4、抗衰老"},{"text":"茶叶当中含有较多的茶多酚、儿茶素等自然成分，能有效提高SOD的活性，从而延缓衰老。"},{"text":"5、防龋齿"},{"text":"六安蓝茶当中的氟能够与牙齿钙质发生化学反应在牙齿表面形成保护膜从而达到固牙防，除此之外，适量饮用六安蓝茶茶还能防口臭。"}]}]}